
import { defineComponent } from "vue";

export default defineComponent({
  name: "app-themes",
  props: { themes: Array, current: { type: String, default: "default" } },
  emits: ["themeChange"],
  setup(_, context) {
    const clickHandler = (value: string) => {
      context.emit("themeChange", value);
    };

    return { clickHandler };
  },
});
