
import { defineComponent, ref } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "app-search-form",
  setup() {
    const query = ref("");
    const router = useRouter();

    const searchSubmit = () => {
      router.push(`/search?s=${query.value}`);
      query.value = "";
    };

    return { query, searchSubmit };
  },
});
